@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&family=Oswald:wght@200;300&display=swap');

body{
    background: black;
}
.clock{
    position: absolute;
    top: 50%;
    left: 50%;
    color: #17D4FE;
    transform: translateX(-50%) translateY(-50%);
    font-size: 60px;
    font-family: 'Orbitron';
    letter-spacing: 7px;
}